* {
  box-sizing: border-box;
}
:root {
  --structuralElement: #5f5f5f;
  --redColorBlock: #ac0505a4;
  --lightColorBlock: #a5a5a5;
  --pixboarder: solid 10px var(--lightColorBlock);
}
  body {
      font-family: Arial, Helvetica, sans-serif;
      margin: 0;
      display: flex;
      flex-direction: column;
      min-height: 100vh;
      justify-content: flex-start;
    }
.header {
  height: 150px;
  background-color: var(--structuralElement);
  border-bottom: 10px solid var(--redColorBlock);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#my-name {
  margin-left: 100px;
  color: white;
  font-size: 180%;
}

.nav-item {
  /* display: block; */
  margin: 20px;
  color: var(--lightColorBlock);
  padding: 14px 20px;
  text-decoration: none;
  text-align: center;
}

.nav-item:hover {
  background-color: var(--lightColorBlock);
  color: #fff;
  text-decoration: underline;
}

.about-me {
  display: flex;
  flex-direction: row;   
}

.previous-work {
  display: flex;
  flex-direction: row;  
  align-items: flex-start; 
}

.previous-project {
  display: flex;
  flex-direction: column;
}

.previous-work-content {
  margin-left: 45px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.PWImg {
  min-width: 40%; 
  max-width: 75%;
  height: auto;
  border: var(--pixboarder);
}
.contact {
  display: flex;
  flex-direction: row;   
 
}
.contact-content{
  align-items: center;
  margin: 20px;
}
.contact-content a {
  margin: 20px;
  color: var(--structuralElement);
  text-decoration: none;
}

.aside {
  width: 200px;
  height: auto;
  margin-right: 20px;
  border-bottom: 10px solid var(--redColorBlock);
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.aside h2 {
  margin: 20px;
}

.about-me-content {
  display: flex;
  flex-direction: row;
  align-items: center;
  
}
.about-me-content img {
  border: var(--pixboarder);
}
.about-me-content p {
  color: var(--redColorBlock);
  margin: 20px;
}
  
#about-me-img {
  height: 300px;
  margin: 20px;
}

.footer {
  display: flex;
  justify-content: center;
  padding: 2%;
  background-color: var(--structuralElement);
  color: #fff;
  }

  .projectTitle {
    margin: 15px;
    font-weight: bold ;
    font-size: larger;

  }
@media screen and (max-width 992px) {
  section {
    width: 50%;
  }
}

@media screen and (max-width 768px) {
  section {
    width: 100%;
  }
}

@media screen and (max-width 576) {
  section {
    width: 100%;
  }
}